<template>
	<div class="person">
		<div id="qr-img" @click="userqr"><img src="../../assets/shops/icons/qr-white.png"/></div>
		<div class="clear"></div>
    <header class="person_header">
      <div class="pic_img">
        <img src="../../assets/per_default.png" alt="">
      </div>
      <p class="person_name" style="color: #fff;">adsadasd</p>
      <p class="person_id"></p>      
    </header>
    <div class="orders-wrap">
    <div class=" title orders" @click="orders">
    	<div>我的订单</div>
    	<div>全部订单></div>
    </div>
    <ul class="nav">
      <li v-for="(item, key) in navs" @click="enterInfo(item, key)">
        <img :src="item.pic | imgFilter" alt=""><p class="hint" v-if="item.num !== 0">{{item.num}}</p><br>
        <span class="nav_font">{{item.name}}</span>
      </li>
    </ul>
    </div>
    <div id="tools-wrap">
    <div class="title">常用工具</div>
    <ul id="tools">
    	<li @click="wallet">
    		<img src="../../assets/shops/user/user-wallet.png"/>
    		<div>我的钱包</div>
    		<p>￥100.00</p>
    	</li>
    	<li @click="jifen">
    		<img src="../../assets/shops/user/user-jifen.png"/>
    		<div>我的积分</div>
    		<p>积分兑礼品</p>
    	</li>
    	<li @click="team">
    		<img src="../../assets/shops/user/user-team.png"/>
    		<div>我的团队</div>
    		<p>分享团队经济</p>
    	</li>
    	<li @click="vip">
    		<img src="../../assets/shops/user/user-vip.png"/>
    		<div>会员中心</div>
    		<p>最高返利30%</p>
    	</li>
    </ul>
    </div>
    <!--<button @click="wuliu">物流</button>
    <button @click="orderdetail">orderdetail</button>-->
	</div>
</template>
<script type="text/javascript">
  import { Toast } from 'mint-ui';
  export default{
		data(){
			return {
			    state:{},
          navs: [
            {name: '待付款', pic: 'order-icon1.png',num:0},
            {name: '待发货', pic: 'order-icon2.png',num:0},
            {name: '已发货', pic: 'order-icon3.png',num:0},
            {name: '已完成', pic: 'order-icon4.png',num:0}
          ],
        sorts: [
          {name: '我的钱包', pic: 'sort1.png'},
          {name: '我的麦粒', pic: 'sort2.png'},
          {name: '我的级别', pic: 'sort3.png'},
          {name: '我的邀请', pic: 'sort4.png'},
          {name: '去开店', pic: 'sort5.png'},
          {name: '开店礼包', pic: 'sort6.png'},
          {name: '麦粒商城', pic: 'sort7.png'},
          {name: '个人设置', pic: 'setting.png'},
        ],
        person: {}
			}
		},
    filters: {
		  imgFilter(val){
		    return 'static/shop/'+ val;
      }
    },
    methods: {
    userqr(){
      	this.$router.push('/userqr')
      },
      enterSort(list){
          switch (list.name){
            case '我的麦粒':
              this.$router.push('/sign');
              break;
            case '我的钱包':
              this.$router.push('/wallet');
              break;
            case '我的级别':
              this.$router.push('/rank');
              break;
            case '我的邀请':
              this.$router.push('/invite');
              break;
            case '去开店':
              this.$parent.setTab('setShop');
              this.$router.push('/setShop');
              break;
            case '开店礼包':
              let type = this.person.type;
              if (type === 0) {
                Toast({
                  message: '请开店后再来',
                  duration: 1000
                });
              } else {
                this.$router.push('/gift');
              }
              break;
            case '麦粒商城':
              this.$parent.setTab('defenShop');
              this.$router.push('/defenShop');
              break;
            case '个人设置':
              this.$router.push('/setting');
              break;
          }
      },
      enterSign(){
        this.$router.push('/sign');
      },
      enterInfo(item,index){
        let url = '/order/' + index;
        this.$router.push(url);
      },
      getGoodsInfo(){
        this.post('home/User/getUserInfo',{}).then(res => {
          if (res.data.code === 10000) {
            this.person = res.data.info;
          }
        })
      },
      invites(){
        if (this.person.type !== 0){
            this.$router.push('/inviteCode')
        } else {
          Toast({
            message: '请开店后再来获取二维码',
            duration: 1000
          });
        }
      },
      vip(){
      	this.$router.push('/vip')
      },
      wallet(){
      	this.$router.push('/wallet')
      },
      jifen(){
      	this.$router.push('/jifen')
      },
      team(){
      	this.$router.push('/team')
      },
//	  wuliu(){
//	  	this.$router.push('/wuliu')
//	  },
	  orders(){
	  	this.$router.push('/orders')
	  },
//	  orderdetail(){
//	  	this.$router.push('/orderdetail')
//	  },
      //获取未发货的
      getGoodsState() {
          this.post('home/Order/getOrderNumber', {}).then(res => {
            if (res.data.code === 10000) {
                this.navs[0].num = res.data.info.a;
                this.navs[1].num = res.data.info.b;
                this.navs[2].num = res.data.info.c;
            }
          })
      }
    },
    mounted(){
		  this.getGoodsState();
		  //获取首页信息
      this.getGoodsInfo();
    }
	}
</script>
<style type="text/css" scoped>
#qr-img{float: right;padding: .3rem .5rem;padding-bottom: 0;}
#qr-img img{width: .5rem;height: .5rem;}
  .person {
    width: 100%;
    position: relative;
    /*background: url("../../assets/shopImage/bg.png") no-repeat;*/
   background: url(../../assets/shops/imgs/user-bg.png)no-repeat;
    background-size: contain;
    overflow-y: auto;
  }
  .person_header {
    margin: 1.5rem auto 0 auto;
    /*padding-top: 0.3rem;*/
    padding-bottom: 0.1rem;
    width: 90%;
    /*background-color: #ffffff;*/
    border-radius: 2px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    position: relative;
    /*box-shadow: 0 1px 2px 0 rgba(249, 249, 249, 0.5);*/
  }
  .pic_img {
    width: 1.1rem;
    padding-top: 1.1rem;
    border-radius: 100%;
    background-color: #F2F2F2;
    overflow: hidden;
    position: relative;
  }
  .pic_img img {
    position: absolute;
    top: 0;
    width: 100%;
    min-height: 100%;
  }
  .person_name {
    font-size: 0.26rem;
    color: #111111;
    margin: 0.1rem 0 0.16rem 0;
  }
  .person_id {
    font-size: 0.22rem;
    color: #666666;
  }
  .person_info {
    width: 100%;
    height: 1.5rem;
    background: url("../../assets/shopImage/bg_h.png") no-repeat scroll;
    /*background-attachment: scroll;*/
    background-size: auto 1.5rem;
    display: flex;
  }
  .person_info section {
    flex: 1;
    text-align: center;
    margin: 0.4rem 0 0.3rem 0;
  }
  .person_info section:first-child {
    padding-left: 0.2rem;
  }
  .person_info section:nth-child(2) {
    padding-right: 0.2rem;
  }
 /*样式*/
  .font {
    color: #ff6f00;
    font-size: 0.32rem;
  }
  .font_t {
    font-size: 0.24rem;
    color: #333333;
  }
  .set {
    width: 0.36rem;
    position: absolute;
    top: 0.2rem;
    right: 0.2rem;
  }
  .person_header .charge {
    display: inline-block;
    width: .98rem;
    height: 0.4rem;
    color: #ff6f00;
    font-size: 0.22rem;
    border: 1px solid #ff6f00;
    border-radius: 0.2rem;
    text-align: center;
    line-height: 0.4rem;
    position: absolute;
    right: 0.2rem;
    bottom: 0.68rem;
  }
  .nav {
    display: flex;
  }
  .nav li {
    flex: 1;
    text-align: center;
    padding: 0.3rem 0;
    position: relative;
  }
  .hint {
    position: absolute;
    top: 0.1rem;
    right: .42rem;
    color: #FFFFFF;
    border-radius: 50%;
    background-color: #ff6f00;
    display: block;
    width: 20px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    border: 1px solid #ff6f00;
  }
  .nav img {
    width: 0.54rem;
  }
  .nav_font {
    font-size: 0.22rem;
    color: #333333;
  }
  .line {
    width: 100%;
    height: 0.2rem;
    background-color: #F2F2F2;
  }
  .sorts {

  }
  .sorts li {
    padding: 0.2rem 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #F2F2F2;
  }
  .sorts li:nth-child(4) {
    border-bottom: 0.2rem solid #F2F2F2;
  }
  .sorts li:last-child {
    border-top: 0.2rem solid #F2F2F2;
  }
  .sort_img {
    width: 0.3rem;
  }
  .sort_font {
    font-size: 0.28rem;
    color: #111111;
    vertical-align: middle;
    margin-left: 0.2rem;
  }
  .right {
    width: .14rem;
    height: 13px;
  }
 /*我的订单*/
.orders-wrap{
	background: #fff;
	border-radius: 4px;
	margin: .2rem;
	/*margin-top: 1.6rem;*/
}
.orders{
	display: flex;
	flex-direction: row;	
	justify-content: space-between;
	align-items: center;
	margin: .2rem;
}
  /*常用工具*/
 #tools-wrap{
	padding: .2rem;
	padding-top: 0;
	background: #fff;
	margin: .2rem;
	border-radius: 4px;
 }	
 #tools-wrap .title,.orders-wrap .title{
 	padding: .2rem 0;
 	border-bottom: 1px #E6E6E6 solid;
 	margin-bottom: .35rem;
 }
 #tools-wrap .title div{
 	
 }
 #tools{
 	background: #fff;
 	border-radius: 4px;
 	display: flex;
 	flex-direction: row;
 	text-align: center;
 }
 
 #tools li{
 	flex: 1;
 }
 #tools img{
 	width: .6rem;
 	height: .6rem;
 	display: block;
 	margin: auto;
 	margin-bottom: .2rem;
 }
 #tools p{
 	color: #999;
 	font-size: .22rem;
 }
</style>



